div.gallery {
  margin: 5px;
  border: 1px solid #ccc;
  /* float: left; */
  width: 90%;
}

div.gallery:hover {
  border: 1px solid #777;
}

div.gallery img {
  width: 100%;
  height: auto;
}

div.desc {
  padding: 6px;
  text-align: center;
  background: rgb(180, 180, 180);
}

.banner {
  position: relative;
  z-index: 1;
  mix-blend-mode: lighten;
}
.banner img {
  width: 100%;
  height: auto;
  position: absolute;
}

.city-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2%;
}

#banner {
  background: #eb01a5;
  background-image: url("/public/images/texas-national-park.jpg"); /* fallback */
  background-image: url("/public/images/texas-national-park.jpg"),
    linear-gradient(to bottom, #eb01a5, transparent);
  /* background-repeat: no-repeat; */
}

.city-card {
  padding: 12px;
  background: ivory;
  z-index: 10;
  margin: 10px 5px;
  border-radius: 10px;
  border: 1px solid ivory;
  /* width: 45%; */
}

/* .city-card ul {
  padding-left: 10px;
} */

.city-card ul li {
  /* margin-left: 16px; */
  padding-left: 5px;
  list-style-type: "\25A8";
  /* text-align: justify; */
  /* text-justify: ; */
}

.city-card ul li::marker {
  color: rgb(197, 203, 203);
  font-size: 12px;
}

ul li#pro {
  /* list-style-type: "\2714"; */
}

ul li#pro::marker {
  color: rgb(100, 225, 100);
}

ul li#con {
  /* list-style-type: "\2718"; */
}
ul li#con::marker {
  color: red;
}

